/* Global Variables */
@import url('Variables.css');

.top-button{
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 0;
    bottom: 0;
    margin: 80px;
    border-radius: 50%;
    background: var(--color1);
    color: var(--color5);
    font-size: 1rem;
    height: 30px;
    width: 30px;
    outline: 2px solid var(--color5);
    border: none;
    transition: all 200ms ease-in-out;
    animation: showBtn 500ms;
    cursor: pointer;
    z-index: 999;
}

.top-button:hover{
    background: var(--color5);
    color: var(--color1);
    outline: 2px solid var(--color1);
}

.top-button:active{
    transform: scale(1.5);
}


@keyframes showBtn {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}


/* RESPONSIVE */

@media  screen and (max-width: 800px) {
    .top-button{
        display: none;
    }
}