/* General Variables */
@import url('Variables.css');

.about_container{
    display: flex;
    justify-content: center;
    align-items: center; 
    width: 100%;
    background-color: var(--color2);
}

.about{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    width: 80%;
}

.about-img{
    width: 50%;
}

.about_info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 40%;
    height: auto;
    margin: 50px;
}

.about_text01{
    font-size: 3.3rem;
    font-family: var(--fontMain);
    color: var(--color1);
}

.about_text02{
    font-size: 1.4rem;
    font-family: var(--fontSecondary);
    margin: 20px 0px 20px 0px;
    color: var(--color4);
}

.about_download_link{
    display: block;
    background-color: var(--color1);
    color: var(--color5);
    text-align: center;
    margin: 10px 10px 0px 0px;
    height: 45px;
    width: 200px;
    line-height: 30px;
    font-family: var(--fontMain);
    text-decoration: none;
    border-radius: .5rem;
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: 1.5px;
    border: 3px solid var(--color1);
    transition: all 200ms ease;
}

.about_download_link:hover{
    background-color: var(--color5);
    color: #001d3d;
}

.gs_reveal {
    opacity: 0;
    visibility: hidden;
    will-change: transform, opacity;
  }

/* REPONSIVE - MEDIA QUERY */
@media screen and (max-width: 1260px) {
    .about_info{
        width: 80%;
        align-items: center;
    }
    .about_text01{
        font-size: 3.5rem;
    }
    
    .about_text02{
        font-size: 1.4rem;
    }

    .about_download_link{
        width: 300px;
    }

    .about-img{
        width: 80%;
    }
}

@media screen and (max-width: 1080px) {
    .about{
        width: 90%;
    }

    .about_info{
        width: 90%;
    }
    .about_text01{
        font-size: 4rem;
    }
    
    .about_text02{
        font-size: 1.6rem;
    }

    .about_download_link{
        width: 300px;
    }

    .about-img{
        width: 90%;
    }
}

@media screen and (max-width: 540px) {
    .about_info{
        width: 90%;
        margin: 20px;
    }

    .about_text01{
        font-size: 3.3rem;
    }

    .about_text02{
        font-size: 1.5rem;
    }
}

@media screen and (max-width: 400px) {
    .about_info{
        width: 100%;
    }
    
    .about_text01{
        font-size: 3.3rem;
    }

    .about_text02{
        font-size: 1.5rem;
    }
}

@media screen and (max-width: 345px) {
    .about_info{
        width: 100%;
    }
    
    .about_text01{
        font-size: 2.5rem;
    }

    .about_text02{
        font-size: 1.3rem;
    }
}