/* Global Variables */
@import url('Variables.css');

.skills_container{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    min-height: 90vh;
    max-height: auto;
    width: 100%;
    background: var(--color2);
}

.skills_tittle{
    font-family: var(--fontMain);
    color: #343a40;
    letter-spacing: 1px;
    font-size: 2.5rem;
    margin: 100px 0px 50px 0px;
}

.skills_tittle_line{
    height: 4px;
    width: 100%;
    margin-top: 10px;
    background-color: var(--color1);
    transition: 400ms ease;
    outline: none;
    border: none;
}

.skills_buttons_container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: auto;
    width: 1100px;
    margin: 0px 0px 100px 0px;
    padding: 10px;
    border-radius: 25px;
}

.skills_icon_container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
    padding: 15px;
    width: 140px;
    border-radius: 15px;
}

.skills_icon{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    margin-bottom: 20px;
    border-radius: 100%;
    cursor: pointer;
    transition: 300ms ease;
    opacity: .5
}

.skills_icon_logo{
    width: 40px;
    filter: invert(.7);
}

.skills_icon:hover{
    transform: translateY(-5px);
}

.skills_icon_name{
    font-family: var(--fontSecondary);
    font-size: 1.2rem;
    color: var(--color4);
}


/* RESPONSIVE - MEDIA QUERY */

@media screen and (max-width: 1140px){
    .skills_buttons_container{
        width: 80%;
    }
}

@media screen and (max-width: 430px){
    .skills_buttons_container{
        width: 100%;
    }

    .skills_icon_logo{
        width: 50px;
        filter: invert(.7);
    }

    .skills_icon_name{
        font-family: var(--fontMain);
        font-size: 1.4rem;
        letter-spacing: 1px;
        color: var(--color3);
    }
}




