.home_container{
    display: flex;
    width: 100%;
    min-height: 920px;
    max-height: 1200px;
    position: relative;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-image: url(../img/Background.svg);
    background-size: cover;
}

.home_container::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 20vh;
    background: linear-gradient(transparent, var(--color2), var(--color2) );
    bottom: 0%;
    z-index: 100;
}

.header_text{
    display: block;
    position: absolute;
    margin: 20px;
    text-align: center;
    font-family: var(--fontMain);
    font-size: 8rem;
    text-transform: uppercase;
    letter-spacing: 5px;
    color: var(--color5);
    top: 20%;
    right: 12%;
    z-index: 2;
    transition: ease-out;
    transition-duration: 600ms;
    animation: appearing 3s;
}

.floating-planet{
    position: absolute;
    height: 80%;
    top: 13%;
    left: 5%;
    border: none;
    outline: none;
    transition: ease-out;
    transition-duration: 700ms;
    animation: appearing 1s;
    z-index: 1;
}

.astronaut{
    position: absolute;
    left: 7%;
    height: 100%;
    border: none;
    outline: none;
    transition: ease-out;
    transition-duration: 500ms;
    animation: appearing 2s;
    z-index: 3;
}

.clouds{
    display: block;
    position: absolute;
    line-height: 0;
    bottom: -35%;
    outline: none;
    border: none;
    z-index: 4;
    transition: ease-out;
    transition-duration: 1000ms;
    animation: appearing 500ms;
}

.star-box{
    position: absolute;
    width: 100%;
    height: 100%;
    background: none;
    border: none;
    outline: none;
    transform: rotateZ(-20deg);
    z-index: 2;
}

.star{
    position: absolute;
    left: 50%;
    top: 50%;
    height: 3px;
    background: linear-gradient(-45deg, transparent, #fff);
    border-radius: 999px;
    filter: drop-shadow(0 0 6px #fff);
    animation: tail 5s ease-in-out infinite, 
                      falling 5s ease-in-out infinite;
}

@keyframes tail {
    0%{
        width: 0px;
    }
    30%{
        width: 300px;
    }
    100%{
        width: 0px;
    }
}

@keyframes falling {
    0%{
        transform: translateX(0);
    }
    0%{
        transform: translateX(100vw);
    }
}

.star:nth-child(1){
    top: calc(50% - 500px);
    left: calc(50% - 300px);
    animation-delay: 1s;
}


.star:nth-child(2){
    top: calc(50% - 100px);
    left: calc(40% - 100px);
    animation-delay: 5s;
}

.star:nth-child(3){
    top: calc(80%  - 100px);
    left: calc(50% - 150px);
    animation-delay: 3s;
}


@keyframes appearing {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

