/* Global Variables */
@import url('Variables.css');


.value-container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    /* height: 200px; */
    border-radius: 0px 0px 150px 150px;
    background: var(--color1);
    border-bottom: 8px solid rgba(0, 0, 0, 0.226);
}

.value-text{
    font-family: var(--fontMain);
    font-size: 2.5rem;
    text-align: center;
    width: 500px;
    margin: 40px 5px 40px 5px;
    color: var(--color5);
}

@media screen and (max-width: 1024px) {
    .value-text{
        font-size: 2.2rem;
        width: 300px;
        margin: 40px 5px 40px 5px;
    }
}

@media screen and (max-width: 620px) {
    .value-text{
        font-size: 2.5rem;
        width: 350px;
        margin: 40px 5px 40px 5px;
    }

}

@media screen and (max-width: 530px) {
    .value-text{
        font-size: 2.5rem;
        width: 400px;
        margin: 30px 10px 30px 10px;
    }

    .text-one{
        margin-bottom: 0;
    }
}

@media screen and (max-width: 380px) {
    .value-container{
        border-radius: 0px 0px 100px 100px;
    }

    .value-text{
        font-size: 2rem;
        width: 400px;
        margin: 30px 10px 30px 10px;
    }
}