/* Global Variables */
@import url('Variables.css');

.portfolio_container{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    height: auto;
    width: 100%;
    background: var(--color2);
}

.portfolio_tittle{
    font-family: var(--fontMain);
    color: #343a40;
    letter-spacing: 1px;
    font-size: 2.5rem;
    margin: 100px 0px 50px 0px;
}

.portfolio_tittle_line{
    height: 4px;
    width: 100%;
    margin-top: 10px;
    background-color: var(--color1);
    transition: 400ms ease;
    outline: none;
    border: none;
}

.portfolio_project{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 80%;
    min-height: 400px;
    margin-bottom: 100px;
    border-radius: 20px;
}


/*  Project Card */

.project-card-box{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: var(--color5);
    border-radius: 20px;
    min-width: 300px;
    height: 300px;
    margin: 20px;
    box-shadow: 2px 3px 5px 1px rgba(0, 0, 0, 0.124);
    transition: all 200ms ease-in-out;
}

.project-card-box:hover{
    box-shadow: 5px 5px 5px 1px rgba(0, 0, 0, 0.124);
}

.box-one{
    background-image: url('../img/CardCover01.svg');
    background-size: 300px 300px;
    background-repeat: no-repeat;
}

.box-two{
    background-image: url('../img/CardCover02.svg');
    background-size: 300px 300px;
    background-repeat: no-repeat;
}

.box-tree{
    background-image: url('../img/CardCover03.svg');
    background-size: 300px 300px;
    background-repeat: no-repeat;
}

.project-card-content{
    display: flex;
    overflow: hidden;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-size: cover;
    width: 100%;
    height: 100%;
    font-size: 1.2rem;;
}

.project-card-content-title{
    font-size: 2.2rem;
    font-family: var(--fontMain);
    color: var(--color1);
    font-weight: bold;
    letter-spacing: 1px;
    margin: 10px 0px;
}

.project-card-content-desc{
    font-family: var(--fontSecondary);
    font-weight: 800;
    color: var(--color3);
}

.project-card-button{
    color: var(--color1);
    background: none;
    font-size: 2rem;
    margin: 20px;
    border-radius: 50px;
    border: none;
    outline: none;
    cursor: pointer;
    transform: rotate(0deg);
    transition: all 200ms ease-in-out;
}

.project-card-button:hover{
    transform: scale(1.2);
    color: var(--color3);
}

.button-rotate{
    color: var(--color1);
    background: none;
    font-size: 2.2rem;
    margin: 20px;
    border-radius: 50px;
    border: none;
    outline: none;
    cursor: pointer;
    transform: rotate(180deg);
}

.project-card-description{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 0;
    width: 0;
    border-radius: 100%;
    overflow: hidden;
    transition: all 200ms ease-in-out;
}

.project-card-description-active{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 300px;
    width: 100%;
    overflow: hidden;
    background-color: var(--color1);
    color: var(--color5);
    transition: all 200ms ease-in-out;
}

.project-card-description-desc{
    font-family: var(--fontSecondary);
    width: 90%;
    text-align: center;
    font-size: 1.2rem;
}

.project-card-btn{
    display: block;
    margin: 15px;
    padding: 10px 20px;
    color: var(--color1);
    background-color: var(--color5);
    text-decoration: none;
    font-weight: bold;
    font-size: 1.2rem;
    font-family: var(--fontSecondary);
    border-radius: 50px;
    outline: 3px solid var(--color1);
    border: none;
    transition: all 200ms ease-in-out;
}

.project-card-btn:hover{
    display: block;
    margin: 15px;
    padding: 10px 20px;
    color: var(--color5);
    background-color: var(--color1);
    text-decoration: none;
    font-weight: bold;
    font-size: 1.2rem;
    font-family: var(--fontSecondary);
    border-radius: 50px;
    outline: 2px solid var(--color5);
    border: none;
}

@media screen and (max-width: 1080px) {
    .portfolio_project{
        width: 100%;
    }
}

@media screen and (max-width: 450px) {
    .project-card-box{
        min-width: 85%;
    }

    .box-one,
    .box-two,
    .box-tree{
        background-size: 100% 100%;
    }

    .project-card-content-title{
        font-size: 2.5rem;
    }
}


