/* General Variables */
@import url('Variables.css');

.hireme{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    background: var(--color1);
    height: 130px;
}

.hireme_container{
    display: flex;
    align-items: center;
    flex-direction: row;
    color: var(--color5);
}

.hireme_tittle{
    font-family: var(--fontMain);
    font-size: 2.5rem;
    letter-spacing: 2px;
}

.hireme_tittle_line{
    height: 4px;
    width: 10%;
    background-color: var(--color5);
    margin-bottom: 20px;
    transition: 400ms ease;
    outline: none;
    border: none;
}

.hireme_tittle_line:hover{
    width: 20%;
}

.astro_mini{
    width: 65px;
    margin-right: 20px;
}

.hireme_button{
    display: block;
    background: var(--color5);
    color: var(--color1);
    text-align: center;
    font-weight: bold;
    height: 50px;
    width: 200px;
    font-family: var(--fontMain);
    text-decoration: none;
    border-radius: .5rem;
    font-size: 1.6rem;
    font-weight: 600;
    letter-spacing: 2px;
    border: 3px solid var(--color5);
    cursor: pointer;
    transition: all 200ms ease-in-out;
}

.hireme_button:hover{
    background-color: var(--color1);
    color: var(--color5);
}

/* RESPONSIVE - MEDIA QUERY */

@media screen and (max-width: 780px){
    .hireme_tittle{
        font-size: 2rem;
    }
}

@media screen and (max-width: 690px){
    .hireme{
        flex-wrap: wrap;
        height: auto;
    }

    .hireme_container{
        margin: 40px 0px;
        display: flex;
        align-items: center;
        flex-direction: row;
        color: var(--color5);
    }

    .hireme_tittle{
        font-size: 3rem;
    }

    .hireme_button{
        height: 50px;
        width: 300px;
        margin-bottom: 40px;
    }
}

@media screen and (max-width: 570px){
    .hireme_tittle{
        font-size: 2.2rem;
    }

    .hireme_button{
        height: 50px;
        width: 300px;
        margin-bottom: 40px;
    }
}

@media screen and (max-width: 490px){
    .hireme_container{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        flex-direction: column;
        color: var(--color5);
        margin: 10px;
    }

    .astro_mini{
        width: 80px;
        margin: 20px;
    }

    .hireme_tittle{
        font-size: 2.5rem;
        margin: 10px;
    }

    .hireme_button{
        height: 50px;
        width: 300px;
        margin-bottom: 40px;
    }
}

@media screen and (max-width: 470px){
    .hireme_tittle{
        font-size: 2rem;
    }

    .hireme_button{
        width: 280px;
    }
}

@media screen and (max-width: 380px){
    .hireme_tittle{
        font-size: 1.5rem;
    }

    .hireme_button{
        font-size: 1.6rem;
        width: 200px;
    }
}