/* General Variables */
@import url('Variables.css');

.footer{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin: 0; 
    height: 120px;
    width: 100%;
    color: #fff;
    background-color: var(--color1);
    overflow: hidden;
}

.footer_text{
    font-family: var(--fontMain);
    font-size: 1.5rem;
    letter-spacing: 5px;
    margin-right: 20px;
}

.footer_icon_container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 60px;
    width: 200px;
    overflow: hidden;
}

.footer_icon_container, a{
    display: flex;
    height: 40px;
    width: auto;
    margin: 15px 10px;
    padding: 5px;
}

.footer_social{
    height: 30px;
    width: 30px;
    filter: invert(1);
    cursor: pointer;
    transition: all 200ms ease-in-out;
}

.footer_social:hover{
    filter: invert(0.1);
}

.footer_cr{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 1rem;
    width: 300px;
    z-index: 10;
}

@media screen and (max-width: 690px) {
    .footer{
        height: auto;
    }

    .footer{
        flex-direction: column;
    }

    .footer_cr{
        margin-top: 30px;
    }

    .footer_icon_container{
        display: flex;
        height: 40px;
        width: auto;
        margin: 20px 10px;
        padding: 25px;
    }
}

@media screen and (max-width: 425px) {
    .footer_text{
        font-size: 1.7rem;
    }
}

@media screen and (max-width: 390px) {
    .footer_text{
        display: none;
    }
}
