/* General Variables */
@import url('Variables.css');

.contact_container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    width: 100%;
    background-color: var(--color2);
}


.contact_form_container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: 50px 0px 50px 0px;
    width: 90%;
}

.astro-img{
    position: relative;
    left: 0;
    width: 50%;
}

.contact_tittle{
    font-family: var(--fontMain);
    color: #343a40;
    letter-spacing: 1px;
    font-size: 2.5rem;
    margin: 100px 0px 50px 0px;
}

.contact_tittle_line{
    height: 4px;
    width: 100%;
    margin-top: 10px;
    background-color: var(--color1);
    transition: 400ms ease;
    outline: none;
    border: none;
}


/* Contact Form */

.contact_form{
    display: flex;
    flex-wrap: wrap;
    max-width: 600px;
    margin: 20px;
    padding: 20px;
}

.contact_form_label{
    font-family: var(--fontMain);
    font-size: 1.5rem;
    font-weight: 600;
    letter-spacing: 4px;
    color: var(--color1);
    width: 500px;
    margin: 20px 10px 10px 10px;
}

.contact_form_input{
    font-family: var(--fontSecondary);
    color: var(--color3);
    font-size: 1rem;
    font-weight: 400;
    padding: 10px;
    width: 100%;
    height: 40px;
    margin-bottom: 5px;
    border: none;
    outline: none;
    border-radius: 50px;
    box-shadow: 2px 8px 10px 3px var(--shadow1);
    transition: 200ms ease;
}

.contact_form_textarea{
    font-family: var(--fontSecondary);
    color: var(--color4);
    font-size: 1rem;
    font-weight: 400;
    padding: 10px;
    width: 100%;
    height: 150px;
    margin-bottom: 10px;
    resize: none;
    border: none;
    outline: none;
    border-radius: 20px;
    box-shadow: 2px 8px 10px 3px var(--shadow1);
    transition: 200ms ease;
}

.contact_form_button{
    background-color: var(--color1);
    font-size: 1.3rem;
    font-weight: 600;
    letter-spacing: 2px;
    color: var(--color5);
    text-align: center;
    margin: 20px 10px;
    height: 50px;
    width: 150px;
    line-height: 45px;
    letter-spacing: 1.6px;
    font-family: var(--fontMain);
    text-decoration: none;
    border-radius: .5rem;
    border: 3px solid var(--color1);
    transition: all 200ms ease;
    cursor: pointer;
}

.contact_form_input:focus,
.contact_form_textarea:focus{
    box-shadow: 3px 10px 10px 5px var(--shadow1);
}

.contact_form_button:hover{
    background-color: var(--color5);
    color: var(--color1);
}

/* Modal Window */
.swal-button {
    background-color: var(--color6);
    border: none;
}

/* RESPONSIVE - MEDIA QUERY */

@media screen and (max-width: 1835px){
    .contact_form{
        width: 800px;
    }
}

@media screen and (max-width: 1690px){
    .contact_form{
        width: 600px;
    }
}

@media screen and (max-width: 1400px){
    .contact_form{
        width: 500px;
    }
}

@media screen and (max-width: 1260px){
    .contact_form{
        width: 400px;
    }
}

@media screen and (max-width: 1120px){

    .contact_form_container{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    .contact_info{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 90%;
    }

    .contact_form{
        width: 100%;
    }
}

@media screen and (max-width: 850px){
    .contact_form_container{
        width: 100%;
    }
}

@media screen and (max-width: 700px){
    .contact_form_button{
        width: 100%;
    }
}

@media screen and (max-width: 610px){
    .astro-img{
        position: relative;
        left: 0;
        width: 90%;
    }

    .contact_form_button{
        width: 100%;
    }
}