/* Global Variables */
@import url('Variables.css');

.main_menu{
    display: flex;
    position: fixed;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    width: 100%;
    height: 70px;
    color: var(--color5);
    box-sizing: border-box;
    background-color: transparent;
    z-index: 1000;
    transition: all 250ms ease-in-out;
}

.main_menu_active{
    display: flex;
    position: fixed;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    width: 100%;
    height: 55px;
    color: var(--color5);
    box-sizing: border-box;
    background: var(--color1);
    box-shadow: 2px 2px 5px 1px rgba(17, 17, 17, 0.282);
    z-index: 1000;
    transition: all 300ms ease-in-out;
}

.menu_container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 50px;
    width: 100%;
}

.menu_container_link{
    display: block;
    line-height: 30px;
    font-family: var(--fontMain);
    font-size: 1.2rem;
    font-weight: bold;
    margin: 10px 10px;
    text-shadow: 0px 2px var(--color1);
    cursor: pointer;
    transition: 150ms ease-in-out;
    border-radius: 10px;
    border-bottom: 5px solid transparent;
}

.menu_container_link:hover{
    border-bottom: 3px solid var(--color2);    
}

.menu_container_link:active{
    border-bottom: 3px solid var(--color1);    
}

.menu_container_active,
.menu_container_hide{
    display: none;
}

.menu_responsive_icon{
    display: none;
    font-size: 1.8rem;
    margin: 10px 20px;
    cursor: pointer;
}

.triangle-logo{
    width: 55px;
    height: 55px;
    cursor: pointer;
    border-radius: 5px 5px 30px 30px;
    outline: 5px solid transparent;
}

@media screen and (max-width: 800px){

    .menu_container{
        display: none;
    }

    .menu_container_active{
        display: flex;
        position: absolute;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0px;
        right: 0;
        width: 0%;
        height: 100vh;
        background: var(--color1) ;
        overflow: hidden;
        transition: all 200ms ease-in-out;
    }

    .menu_container_hide{
        display: flex;
        position: absolute;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0px;
        right: 0;
        width: 100%;
        height: 100vh;
        background: var(--color1) ;
        overflow: hidden;
        transition: all 200ms ease-in-out;
    }

    .menu_container_icon{
        position: absolute;
        top: 0;
        right: 0;
        font-size: 2rem;
        margin: 20px;
        cursor: pointer;
    }

    .menu_container_li{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        text-align: center;
        width: 100%;
        height: 100px;
    }

    .menu_container_link{
      display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        font-family: var(--fontMain);
        text-decoration: none;
        font-style: normal;
        align-items: center;
        text-align: center;
        height: 100px;
        width: 100%;
        transition: all 200ms ease-in-out;
    }

    .menu_container_link:hover{
        background-color:  var(--color6);
        border: none;
        border-radius: 5px;
        width: 100%;
    }

    .menu_container_link:hover{
        border-bottom: none;
    }

    .menu_responsive_icon{
        display: block;
        position: fixed;
        right: 0;
        top: 0;
        margin: 20px;
    }

    .menu_logo{
        margin: 0px 10px;
    }
}

@media screen and (max-width: 650px){
    .menu_container_link{
        font-size: 2rem;
        letter-spacing: normal;
      }
}
